<template>
    <div class="addRegister flexCol">
        <img class="logo" src="../../assets/register/logo.png">
        <!-- 错误提示，预加载组件 -->
        <errAlert :errShow="errShow" :errTxt="errTxt"></errAlert>
        <loading v-if="loading"></loading>
        <alert :alert="alert" :title="title" :text="text"></alert>
        <!-- 验证提示 -->
        <h1 class="validate" v-if="validate">{{validate}}</h1>
        <!-- 图片上传组件 slot插槽增加样式 -->
        <uploadImg @popBaseImg="popBaseImg_1">
            <img src="../../assets/register/idcard_1.png">
            <p>请上传手持身份证照(正面)</p>
            <img src="../../assets/common/add.png">
        </uploadImg>
        <!-- 图片上传组件 slot插槽增加样式 -->
        <uploadImg @popBaseImg="popBaseImg_2">
            <img src="../../assets/register/idcard_2.png">
            <p>请上传手持身份证照(反面)</p>
            <img src="../../assets/common/add.png">
        </uploadImg>
        <button @click="upload" class="btn">提交审核</button>
    </div>
</template>
<script type="text/javascript">
import uploadImg from '@/components/uploadImg'
export default {
    name: 'addRegister',
    data() {
        return {
            loading: false,
            img: {
                baseImage_front: '',
                baseImage_back: ''
            },
            // 提示框
            passWin: false,
            // 报错弹窗
            errShow: false,
            errTxt: '',
            validate: '',
            // 信息提示弹窗
            alert: false,
            title: '',
            text: ''
        }
    },
    // 移除组件后清除表单
    activated() {
        document.title = "智慧医保信息认证";
        this.img.baseImage_front = '';
        this.img.baseImage_back = '';
    },
    methods: {
        // 图片上传
        popBaseImg_1(baseImg) {
            this.img.baseImage_front = baseImg;
        },
        popBaseImg_2(baseImg) {
            this.img.baseImage_back = baseImg;
        },
        // 图片上传接口
        upload() {
            var that = this;
            this.loading = true;
            if (this.img.baseImage_front == undefined || this.img.baseImage_back == undefined) {
                this.validate = '上传信息不完整，缺少照片'
            } else {
                this.validate = '';
                // 发送图片base64位数据
                this.$tools.PostDataToServer(
                    this,
                    this.API.postImg,
                    this.img,
                    function success(res) {
                        that.loading = false;
                        var resData = res.data;
                        // 如果成功，显示提示框1.5s后跳转至首页
                        if (resData.State.Code == 1) {
                            that.alert = true;
                            that.title = "用户实名信息认证";
                            that.text = "<p>您的信息已提交成功,</p>" + "<p>工作人员将会进行审核,</p>" + "<p>请耐心等待</p>";
                            // 定时器跳转页面
                            var pass = setTimeout(function() {
                                that.$router.replace('/');
                                clearTimeout(pass);
                            }, 1500);
                        }
                    },
                    function error(err) {
                        that.loading = false;
                        that.errShow = true;
                        that.errTxt = '出现错误，请重新加载';
                        var errLoading = setTimeout(function() {
                            that.errShow = false;
                            that.errTxt = ''
                            clearTimeout(errLoading);
                        }, 1600)
                    }
                )
            }
        }
    },
    components: {
        uploadImg
    }
}
</script>
<style type="text/css" scoped>
.addRegister {
    position: fixed;
    height: 100%;
    width: 100%;
    background: url('../../assets/register/registerBg.png');
    -webkit-background-size: 100%;
    background-size: 100%;
    z-index: 111;
    -ms-align-items: center;
    align-items: center;
}

.logo {
    width: 4rem;
    margin: 1rem auto 1rem;
}

.btn {
    width: 45%;
}
</style>



